<template>
    <div class="current container">
        <div class="current__one">
            <img class="current__one-asosiy" :src="getCard.imageUrl" alt="">
        </div>
        <div class="current__two">
            <p class="current__two-price">{{ getCard.amount > 0  ? getCard.priceTwo * getCard.amount : getCard.priceTwo }} ₽</p>
            <h2>{{ getCard.title }}</h2>
            <p class="current__two-sm">тонкое тесто, 26 см.</p>
            <div class="main__box-card__box">
                <span v-if="getCard.amount < 1" @click="addCart(getCard.id,getCard.amount)">
                    <p>+</p>
                    <p>Добавить</p>
                    <p v-if="getCard.amount">{{ getCard.amount }}</p>
                </span>
                <span v-else @click="addCart(getCard.id,getCard.amount)">
                    <p>+</p>
                    <p>Добавить</p>
                    <p v-if="getCard.amount">{{ getCard.amount }}</p>
                </span>
            </div>
        </div>
    </div>
    <router-link to="/pizza" class="cart__end-buy__home current__home">Home</router-link>
</template>
<script>
import { mapState,mapMutations } from "vuex";
export default {
    data() {
        return {
            currentCard:null
        }
    },
    created(){
        this.currentCard = this.$route.params.id
    },
    computed:{
        ...mapState(["getUsers"]),
        getCard(){
            return this.getUsers.find(card => card.id == this.currentCard)
        }
    },
    methods: {
        ...mapMutations(["cartPush"]),
        addCart(id,amount){
            let cartVal = {
                id: id
            }
            this.cartPush(cartVal)
        },
    },
}
</script>
